/* CUSTOM CSS BEGINS HERE */

html {
  overflow: hidden;
}

body {
  --ectl-primary: #fff;
  --ectl-secondary: #eee;
  --ectl-terciary: #ddd;
  --ectl-inverse: #2f3136;
  --ectl-muted: #676767;
  --ectl-very-muted: #aaa;

  --ectl-accent-blue: #{$info-color};
  --ectl-accent-orange: #{$primary-color};

  --ectl-text-primary: #000;
  --ectl-text-inverse: #fff;
  --ectl-text-muted: var(--ectl-muted);

  --ectl-text-white: #fff;
  --ectl-text-black: #000;

  --ectl-nav-bg: var(--ectl-accent-blue);

  --ectl-sub-page-background-overlay: linear-gradient(0deg,rgba(44,44,44,.2),rgba(3,161,224,.6));

  &.theme-dark {
    --ectl-primary: #36393f;
    --ectl-secondary: #2f3136;
    --ectl-terciary: #42454d;
    --ectl-inverse: #fff;
    --ectl-muted: #CDCDCD;
    --ectl-very-muted: #888;

    --ectl-text-primary: #fff;
    --ectl-text-inverse: #000;

    --ectl-nav-bg: var(--ectl-primary);

    --ectl-sub-page-background-overlay: #000000AA;
  }

  background-color: var(--ectl-nav-bg);
}

h1, h2, h3, h4, h5, h6 {
  color: var(--ectl-text-primary);
  font-weight: 700;
}

.ectl-text {
  color: var(--ectl-text-primary) !important;
}

.ectl-text-muted {
  color: var(--ectl-text-muted) !important;
}

.ectl-text-inverse {
  color: var(--ectl-text-inverse) !important;
}

.ectl-text-white {
  color: var(--ectl-text-white) !important;
}

.ectl-text-black {
  color: var(--ectl-text-black) !important;
}

.ectl-primary {
  background-color: var(--ectl-primary) !important;
}

.ectl-secondary {
  background-color: var(--ectl-secondary) !important;
}

.ectl-inverse {
  background-color: var(--ectl-inverse) !important;
}

.ectl-font-weight-normal {
  font-weight: 400 !important;
}

.ectl-font-weight-bold {
  font-weight: 700 !important;
}

.ectl-title {
	padding: 0;
	margin-top: 16px !important;
}

p {
  color: var(--ectl-text-primary);
  font-weight: 400;
}

span {
  color: var(--ectl-text-primary);
}

h6 {
  text-transform: unset;
}

hr {
  border-color: var(--ectl-terciary);
}

ul, ol {
  color: var(--ectl-text-primary);

  & > li {
    color: var(--ectl-text-primary);
  }
}

table.content-tables {
  border-collapse: collapse;
  border: 1px solid var(--ectl-terciary);

  & > thead > tr {
    border: 1px solid var(--ectl-terciary);
  }

  & > thead > tr > th {
    border: 1px solid var(--ectl-terciary);
    padding: 8px;

    & > p {
      font-weight: 700;
    }
  }

  & > tbody > tr {
    border: 1px solid var(--ectl-terciary);
  }

  & > tbody > tr > td {
    border: 1px solid var(--ectl-terciary);
  }
}

.wrapper {
  transform: none !important;
}

.navbar-translate {
  transform: none !important;
}

.sidebar-collapse .navbar-collapse::before {
  background: var(--ectl-nav-bg) !important;
  opacity: 1 !important;

}

.fa-discord {
  opacity: 1 !important;
}

.nav-item .nav-link i {
  width: 25px !important;
  display: inline-flex;
  justify-content: center;
}

.sidebar-collapse .navbar-collapse.show::before {
  box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.3) !important;

}

.accordion-button-dark {
  box-shadow: 0px 5px 35px 0px rgba(0, 0, 0, 0.3);
  background-color: #444;
  color: #f4f4f4;
}
.ship-name-display {
  font-weight: 700;
  font-size: calc(1em + 0.2vw);
  color: var(--ectl-text-muted);
}
.rarity-ultra-rare {
  background: linear-gradient(120deg, #fbffca, #baffbf, #a7efff, #ffabff);
}
.rarity-decisive {
  background: linear-gradient(120deg, #fbffca, #baffbf, #a7efff, #ffabff);
}
.rarity-priority {
  background: #ee9;
}
.rarity-super-rare {
  background: #ee9;
}
.rarity-elite {
  background: #c4adff;
}
.rarity-rare {
  background: #9fe8ff;
}
.rarity-common {
  background: #ccc;
}
.ship-rarity-padding {
  padding: 4px;
  border-radius: 4px !important;

  & > img {
    width: 100%;
    aspect-ratio: 1;
  }
}
.eq-rarity-padding {
  border: 4px solid grey;
  padding: 2px;
}
.tooltip.show {
  opacity: 1;
}
.banner-image {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.discord-bar {
  width: 100%;
  color: var(--ectl-text-inverse);
  padding: 6px;
  text-align: center;
  background-color: var(--ectl-inverse)
}

//body.theme-dark .discord-bar {
//  background-color: var(--ectl-secondary);
//  color: var(--ectl-text-primary);
//}

.hover-link:hover {
  text-decoration: underline;
  color: #4bb5ff;
}
.fas {
  vertical-align: middle;
}
.sub-page-background {
  background-image: url("../../img/bg_repeat.jpg");
  background-repeat: repeat;
  background-size: auto;
  min-height: calc(100vh - 62px);
}
.sub-page-background-overlay {
  background: var(--ectl-sub-page-background-overlay);
  min-height: calc(100vh - 62px);
}

.content-tables {
  width: 100%;
}

.content-tables,
.content-tables > tbody > tr,
.content-tables > tbody > tr > td {
  border: 4px double grey;
  padding: 8px;
}

.horizontalList {
  list-style-type: none;
  margin-bottom: 0px;
}

.horizontalList > li {
  display: inline-block;
  margin: 4px;
  vertical-align: top;
}

.horizontalList > li > a > h6 {
  max-width: 124px;
}

.text-center.img-raised.ship-rarity-padding {
  max-width: 124px;
  max-height: 124px;
}

.semanticDropdownDark > div > div {
  background-color: #2f3136 !important;
  color: #fff !important;
  border-color: #878787 !important;
}

.semanticDropdownDark > input {
  color: #fff;
}

.semanticDropdownDark > div > div:hover {
  background-color: #707782 !important;
}

.ui.active.search.dropdown input.search:focus + .text {
  color: var(--ectl-text-primary) !important;
}

.ui.selection.visible.dropdown > .text:not(.default) {
  color: var(--ectl-text-primary) !important;
}

.__react_component_tooltip {
  max-width: 300px;
}

.equipment-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  grid-gap: 15px 5px;

  @media (max-width: 600px) {
    grid-template-columns: repeat(3, 1fr);
  }

  & > .equipment-column {
    display: grid;
    grid-template-rows: subgrid;
    grid-gap: 5px;
    grid-row: span 3;
    text-align: center;

    & > h6 {
      margin: 0;
      width: 100%;
      overflow-wrap: break-word;
      min-width: 0;
      color: var(--ectl-text-primary);
    }

    & > .col {
      padding-left: 12.5%;
      padding-right: 12.5%;
    }
  }
}

.ship-modal-text-section {
  & > div {
    margin-inline: 15px;

    @media (max-width: 600px) {
      margin-inline: 0;
    }

    & > h5 {
      font-weight: 700;
    }
  }
}

.ship-modal-footer {
  display: flex;
  flex-direction: row-reverse;
  gap: 5px;

  & > button {
    border-radius: 1000px;
    background-color: var(--ectl-inverse) !important;
    color: var(--ectl-text-inverse) !important;
  }
}

.ectl-navbar {
  background-color: var(--ectl-nav-bg);
}

li.nav-item > a {
  border-radius: 1000px !important;
}

.ectl-footer {
  background-color: var(--ectl-primary);
  color: var(--ectl-text-primary);
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.15);
  position: relative;
}

ul.faq-ul li {
  margin-top: 0.5em;
  margin-bottom: 0.5em;
  margin-right: 5%;
}

ul.faq-ul li p {
  margin-left: 5%;
}

.bigtxt {
  line-height: 1.61em;
  font-size: 1.2em;
  font-weight: 400;
}

.filter-clear-button {
  margin: 0;
  flex: 0 0 120px !important;
  border-color: var(--ectl-very-muted) !important;
  color: var(--ectl-text-primary) !important;
  background-color: transparent !important;
  box-shadow: none !important;
  border-left: 1px solid var(--ectl-very-muted) !important;

  &:hover {
    border-color: var(--ectl-inverse) !important;
    border-left: 1px solid var(--ectl-inverse) !important;
  }
}

.search-bar {
  background-color: transparent !important;
  color: var(--ectl-text-primary) !important;
  border-color: var(--ectl-very-muted);
  border-left: 1px solid var(--ectl-very-muted) !important;
  border-right: 1px solid var(--ectl-very-muted) !important;

  &:focus {
    background-color: transparent !important;
    color: var(--ectl-text-primary) !important;
    border-color: var(--ectl-inverse);
    border-left: 1px solid var(--ectl-inverse) !important;
    border-right: 1px solid var(--ectl-inverse) !important;
  }

  &::selection {
    background-color: var(--ectl-inverse) !important;
    color: var(--ectl-text-inverse) !important;
  }

  &::-moz-selection {
    background-color: var(--ectl-inverse) !important;
    color: var(--ectl-text-inverse) !important;
  }
}

.ectl-nav-pill {
  color: var(--ectl-text-muted) !important;
  border-radius: 1000px;

  &.active {
    background-color: var(--ectl-inverse) !important;
    color: var(--ectl-text-inverse) !important;
  }

  &:not(.active).ectl-nav-pill-has-ships {
    border: 1px solid var(--ectl-inverse) !important;
  }
}

.accordion__button {
  background-color: var(--ectl-secondary) !important;
  color: var(--ectl-text-primary) !important;
}

.ectl-expandable {
  user-select: none;
  position: relative;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;

  & > i {
    display: inline-block;
    align-self: center;
    height: 8px;
    width: 8px;
    border-bottom: 2px solid currentColor;
    border-right: 2px solid currentColor;
    transform: rotate(45deg);
    transition: transform 0.2s ease-in-out;
  }

  &.collapsed {
    margin: 0;
  }

  &.collapsed > i {
    transform: rotate(-45deg);
  }

  & + div {
    overflow: hidden;
    opacity: 1;
    transition: opacity 300ms ease-in-out;
  }

  &.collapsed + div {
    height: 0;
    opacity: 0;

  }
}

.scroll-top-button {
  background-color: var(--ectl-inverse) !important;
  color: var(--ectl-text-inverse) !important;
}

.filter-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 5px;

  @media (max-width: 990px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (max-width: 766px) {
    grid-template-columns: 1fr;
  }
}

/* CUSTOM CSS ENDS HERE */

body {
  color: $black-color;
  font-size: $font-size-base;
  font-family: $sans-serif-family !important;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

.main {
  position: relative;
  background: $white-color;
}
/* Animations */
.nav-pills .nav-link,
.nav-item .nav-link,
.navbar,
.nav-tabs .nav-link,
.tag,
.tag [data-role="remove"] {
  @include transition($general-transition-time, $transition-ease);
}

//transition for dropdown caret
.card a,
.bootstrap-switch-label:before {
  @include transition($fast-transition-time, $transition-ease);
}

.dropdown-toggle:after,
[data-toggle="collapse"][data-parent="#accordion"] i {
  @include transition-on-prop(
    transform,
    $fast-transition-time,
    $transition-ease
  );
}

.dropdown-toggle[aria-expanded="true"]:after,
[data-toggle="collapse"][data-parent="#accordion"][aria-expanded="true"] i {
  @include rotate-180();
}

.button-bar {
  display: block;
  position: relative;
  width: 22px;
  height: 1px;
  border-radius: 1px;
  background: $white-bg;

  & + .button-bar {
    margin-top: 7px;
  }

  &:nth-child(2) {
    width: 17px;
  }
}

.separator-line {
  height: 2px;
  width: 44px;
  background-color: $default-color;
  margin: 20px auto;

  &.separator-primary {
    background-color: $primary-color;
  }
}

.section-space {
  height: 62px;
  display: block;
}

.pull-left {
  float: left;
}
.pull-right {
  float: right;
}

.title-up {
  text-transform: uppercase;
}
